import {
    Button,
    Icon,
    message,
    Modal,
    Form,
    Checkbox,
    Input,
    Popover,
    Avatar,
    Menu,
    Badge,
    AutoComplete
} from 'ant-design-vue'

const antdComponentsArr = [Button, Icon, message, Modal, Form, Checkbox, Input, Popover, Avatar, Menu, Badge, AutoComplete]

export default {
    install(Vue) {
        antdComponentsArr.forEach(Component => Vue.use(Component))
    }
}