<template>
  <div class="header-wrapper">
    <div class="fixedWrapper">
      <div class="header">
        <div class="lineImg">
          <img src="../assets/image/QQ.jpg" alt />
        </div>
        <p>{{titalName}}</p>
        <a-popover placement="bottomRight" class="popover">
          <template slot="content" trigger="focus">
            <p type="primary" @click="showModal">
              <a-icon type="user-add" />&nbsp;&nbsp;添加好友
            </p>
            <a-modal
              title="添加好友"
              v-model="visible"
              @ok="handleOk"
              class="a-modal"
              okText="添加"
              cancelText="取消"
              :zIndex="zIndex"
              :closable="closable"
            >
              <a-input placeholder="请输入添加好友的用户名" v-model="username" ref="userNameInput">
                <a-icon slot="prefix" type="user" />
                <a-icon slot="suffix" type="close-circle" @click="emitEmpty" />
              </a-input>
            </a-modal>
            <p @click="showCreateModal">
              <a-icon type="message" />&nbsp;&nbsp;创建群聊
            </p>
            <a-modal
              title="创建群聊"
              v-model="creategroup_visible"
              @ok="handleOk_creategroup"
              class="a-modal"
              okText="创建"
              cancelText="取消"
              :zIndex="zIndex"
              :closable="closable"
            >
              <a-input placeholder="请输入您想要创建的群组名" v-model="creatgroup">
                <a-icon slot="prefix" type="message" />
              </a-input>
            </a-modal>
            <p @click="showAddModal">
              <a-icon type="usergroup-add" />&nbsp;&nbsp;添加群聊
            </p>
            <a-modal
              title="添加群聊"
              v-model="addgroup_visible"
              @ok="handleOk_addgroup"
              class="a-modal"
              okText="添加"
              cancelText="取消"
              :zIndex="zIndex"
              :closable="closable"
            >
              <a-input placeholder="请输入添加群组的群组id" v-model="addgroup">
                <a-icon slot="prefix" type="message" />
              </a-input>
            </a-modal>
          </template>
          <span>
            <a-icon type="plus" />
          </span>
        </a-popover>
      </div>

      <div class="search">
        <a-input-search placeholder="搜索" @search="onSearch" class="inputSearch" />
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { mapActions, mapState } from "vuex";
import { message } from "ant-design-vue";
export default {
  name: "Header",
  data() {
    return {
      headTitle: "消息",
      zIndex: 9999,
      closable: false,
      visible: false,
      creategroup_visible: false,
      addgroup_visible: false,
      username: "",
      creatgroup: "",
      addgroup: ""
    };
  },
  computed: {
    ...mapState(["activeTab"]),
    titalName() {
      if (this.activeTab === "messageTital") {
        this.headTitle = "消息";
        return this.headTitle;
      }
      if (this.activeTab === "contacts") {
        this.headTitle = "通讯录";
        return this.headTitle;
      }
      if (this.activeTab === "user") {
        this.headTitle = "我";
        return this.headTitle;
      }
    }
  },
  methods: {
    ...mapActions(["addFriends", "createGroupNew", "joinGroup", "agreeJoinGroup"]),
    onSearch(value) {
      console.log(value);
    },
    showModal() {
      this.visible = true;
    },
    showCreateModal() {
      this.creategroup_visible = true;
    },
    showAddModal() {
      this.addgroup_visible = true;
    },
    emitEmpty() {
      this.$refs.userNameInput.focus();
      this.username = "";
    },
    handleOk() {
      if (this.username.trim()) {
        this.addFriends(this.username);
        this.visible = false;
        message.success("好友请求已发送！");
      }
      this.username = "";
    },
    //创建群聊
    handleOk_creategroup() {
      if (this.creatgroup.trim()) {
        this.createGroupNew(this.creatgroup);
        this.creategroup_visible = false;
        message.success("群组已成功创建！");
      }
      this.creatgroup = "";
    },
    //添加群聊
    handleOk_addgroup() {
      if (this.addgroup.trim()) {
        this.joinGroup(this.addgroup).then((res) => {
          this.agreeJoinGroup(this.addgroup)
          message.success("已成功加入群组！");
        },() => {
          message.warning('您已经在这个群组里了！');
        })
        this.addgroup_visible = false;
      }
      this.creatgroup = "";
    }
  }
};
</script>

