import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login'
import MessageView from './views/MessageView'

Vue.use(Router)

export default new Router({
  mode: 'history',
  //打包放进服务器在http://www.liwencheng9102.cn/dist
  base: '/mobile/',
  scorllBehavior: () => ({
    y: 0
  }),

  routes: [{
      path: '/home',
      redirect: {
        name: 'message'
      },
      name: 'home',
      component: Home,
      children: [{
          path: '/home/message',
          name: 'message',
          component: MessageView
        },
        {
          path: '/home/contacts',
          name: 'contacts',
          component: () => import('./views/Contacts')
        },
        {
          path: '/home/user',
          name: 'user',
          component: () => import('./views/User')
        },
        {
          path: '/home/chatbox/:username',
          name: 'chatbox',
          component: () => import('./views/Chatbox')
        },
        {
          path: '/home/chatgroup/:groupid',
          name: 'chatgroup',
          component: () => import('./views/Chatgroup')
        },
        {
          path: '/home/chatgroup/:groupid/groupdetail',
          name: 'groupdetail',
          component: () => import('./views/Groupdetail')
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: () => import('./views/Register.vue')
    }
  ]
})