<template>
  <div class="about">
    <img alt="logo" src="../assets/image/timg.jpg" />
    <div class="group">
      <input class="user-ipt" type="text" placeholder="用户名" v-model="username" />
    </div>
    <div class="group">
      <input class="user-ipt" type="password" placeholder="密码" v-model="password" />
    </div>
    <div class="btn" :class="{active:isActive}" @click="login">
      <!-- <router-link to="/mine/chatlist"> -->
      <span>
        <a-icon type="arrow-right" />
      </span>
      <!-- </router-link> -->
    </div>

    <div class="linkto">
      <router-link to="/register" class="routerlink">用户注册</router-link>|
      <span>忘记密码</span>
      <div class="bottom">文城出品 必属精品</div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { Modal } from 'ant-design-vue'

export default {
  name: "about",
  data() {
    return {
      username: "",
      password: ""
    };
  },
  computed: {
    isActive() {
      if (this.username && this.password) {
        return true;
      } else {
        return false;
      }
    },
    ...mapState(["loginStatus"])
  },
  methods: {
    ...mapActions(["receivedUser", "loginGetToken"]),
    login() {
      if (this.username && this.password) {
        let user = {
          username: this.username,
          password: this.password
        };
        this.receivedUser(user);
        this.loginGetToken();
      }else{
        Modal.warning({
        title: '输入框不能为空',
      });
      }
    }
  },
  watch: {
    loginStatus(newVal, oldVal) {
      // 观察登录状态的变化
      if (newVal !== oldVal && newVal === true) {
        console.log("登录状态发生变化,并且登录成功");
        this.$router.replace("/home");
      }
    }
  },
  components: {}
};
</script>

