import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import {
  webIMConnection
} from './js/WebIMConnection.js'
/* eslint-disable */
export default new Vuex.Store({
  state: {
    activeTab: 'messageTital',
    WebIM: null,
    user: {
      username: '',
      password: '',
      token: ''
    },
    loginStatus: false, // 登录状态
    roster: [],
    messages: [],
    msgDataSource: {},
    activeUser: '',
    group: [], //群组
    title: '',
    groupMsgData: {},  // {gid:[message,message],gid2:[message,message]}
    activeGroup: ''
  },
  getters: {
    msgList(state) {
      let user = state.activeUser
      let ds = state.msgDataSource
      if (!user || !ds[user]) {
        return []
      } else {
        return ds[user]
      }
    },
    groupMsgList(state) {
      let gid = state.activeGroup
      let md = state.groupMsgData
      if (!gid || !md[gid]) {
        return []
      } else {
        return md[gid]
      }
    }
  },
  mutations: {
    change_tab(state, active) {
      state.activeTab = active
    },
    receive_tokek(state, token) {
      state.user = {
        ...state.user,
        token
      }
    },
    receive_username(state, username) {
      state.user = {
        ...state.user,
        username
      }
    },
    receive_password(state, password) {
      state.user = {
        ...state.user,
        password
      }
    },
    change_login_status(state, loginStatus) {
      state.loginStatus = loginStatus
    },
    RECEIVE_IM(state, WebIM) {
      state.WebIM = WebIM
    },
    receive_roster(state, roster) {
      state.roster = roster
    },
    receive_messages(state, m) {
      state.messages.push(m)
    },
    dec_messages(state, name) {
      state.messages = state.messages.filter(message => message.from !== name)
    },
    addNewMsg(state, msg) {
      let {
        from
      } = msg
      if (state.msgDataSource[from]) { // 当前数据源中已经包含了跟此用户的聊天记录
        state.msgDataSource[from] = [...state.msgDataSource[from], msg]
      } else { // 用户第一次聊天 
        state.msgDataSource = {
          ...state.msgDataSource,
          [from]: [msg]
        }
      }
      //将有未读标志的置顶
      // for (let key in state.msgDataSource) {
      // state.msgDataSource[key].forEach(ele => {
      //   if (ele.badge && ele.badge == 1) {
      //     delete(state.msgDataSource[key])
      //     state.msgDataSource = {
      //       key: state.msgDataSource[key],
      //       ...state.msgDataSource
      //     }
      //   }
      // })
      // }

      //存入本地存储
      let myId = state.user.username
      localStorage.setItem(myId, JSON.stringify(state.msgDataSource))
    },

    //添加群组信息
    addGRoupMsg(state, msg){
      let {
        to
      } = msg
      if (state.groupMsgData[to]) { // 当前数据源中已经包含了跟此群组的聊天记录
        state.groupMsgData[to] = [...state.groupMsgData[to], msg]
      } else { // 群组第一次聊天 
        state.groupMsgData = {
          ...state.groupMsgData,
          [to]: [msg]
        }
      }

      // 存入本地存储
      let groupMsg = 'groupMsg'
      localStorage.setItem(groupMsg, JSON.stringify(state.groupMsgData))
    },

    receive_activeUser(state, user) {
      state.activeUser = user
    },

    receive_activegroup(state, gid) {
      state.activeGroup = gid
    },

    //去除未读标记
    updateUnread(state, name) {
      for (let key in state.msgDataSource) {
        if (key == name) {
          state.msgDataSource[key].forEach(element => {
            if (element.badge && element.badge == 1) {
              element.badge = 2
            }
          });
        }
      }
      //存入本地存储
      let myId = state.user.username
      localStorage.setItem(myId, JSON.stringify(state.msgDataSource))
    },

    //去除群组未读标记
    updateGroupUnread(state, gid) {
      for (let key in state.groupMsgData) {
        if (key == gid) {
          state.groupMsgData[key].forEach(element => {
            if (element.badge && element.badge == 1) {
              element.badge = 2
            }
          });
        }
      }
      // 存入本地存储
      let groupMsg = 'groupMsg'
      localStorage.setItem(groupMsg, JSON.stringify(state.groupMsgData))
    },

    //群组写入state
    receive_group(state, group) {
      state.group = group
    },
  },

  actions: {
    //获取WebIM conn对象并建立环信事件监听的action
    getConnectionAndCreateEventListener({
      commit,
      dispatch
    }, WebIM) {
      // 将WebIM conn对象赋值给了state.conn
      commit('RECEIVE_IM', WebIM)
      // 开启将WebIM conn事件监听
      webIMConnection(WebIM.conn, {
        onOpened: () => {
          commit('change_login_status', true) // 登陆成功 user登录状态为真
        },
        handlePresence: (e) => {
          //（发送者希望订阅接收者的出席信息），即别人申请加你为好友
          if (e.type === 'subscribe') {
            // console.log(e);
            commit('receive_messages', e)
          }

          //(发送者允许接收者接收他们的出席信息)，即别人同意你加他为好友
          if (e.type === 'subscribed') {
            // console.log(e);
            dispatch('getRoster')
          }
        },
        receiveMsg: (e) => {
          //添加未读标记
          // if (!state.activeUser || state.activeUser !== e.from) {
          e = {
            ...e,
            badge: 1
          }
          // }
          commit('addNewMsg', e)
        },
        receiveGroupMsg: (e) => {
          e = {
            ...e,
            badge: 1
          }
          commit('addGRoupMsg', e)
        }
      })
    },
    // 登录返回token的action
    loginGetToken({
      commit,
      state
    }) {
      let {
        username,
        password
      } = state.user
      if (username.trim() && password.trim()) {
        const option = {
          apiUrl: state.WebIM.config.apiURL,
          user: username,
          pwd: password,
          appKey: state.WebIM.config.appkey,
          success: function (token) {
            token = token.access_token;
            commit('receive_tokek', token)
          },
          error: function () {}
        }
        state.WebIM.conn.open(option) // 登录
      }
    },
    // 修改账号密码的action
    receivedUser({
      commit
    }, {
      username,
      password
    }) {
      commit('receive_username', username)
      commit('receive_password', password)
    },
    //注册
    registerWebIM({
      state
    }, {
      username,
      password,
      nickname
    }) {
      return new Promise((resolve, reject) => {
        let options = {
          username,
          password,
          nickname,
          appKey: state.WebIM.config.appkey,
          success: function () {
            resolve()
          },
          error: function (error) {
            // console.log('注册失败')
            reject(error)
          },
          apiUrl: state.WebIM.config.apiURL
        };
        state.WebIM.conn.registerUser(options);
      })
    },

    //添加好友
    addFriends({
      state
    }, username) {
      // console.log('addfriend');

      state.WebIM.conn.subscribe({
        to: username,
        message: '加个好友呗!'
      });
    },

    //同意添加好友
    agreeFriends({
      state,
      commit,
      dispatch
    }, username) {
      state.WebIM.conn.subscribed({
        to: username,
        message: '[resp:true]'
      });
      dispatch('getRoster')
      commit('dec_messages', username)
    },

    //拒绝添加好友
    disagreeFriends({
      state,
      commit
    }, username) {
      state.WebIM.conn.unsubscribed({
        to: username,
        message: 'rejectAddFriend'
      });
      commit('dec_messages', username)
    },

    //获取好友列表
    getRoster({
      state,
      commit
    }) {
      state.WebIM.conn.getRoster({

        success: function (roster) {
          let rosArr = []
          for (var i = 0, l = roster.length; i < l; i++) {
            let ros = roster[i];
            if (ros.subscription === 'both' || ros.subscription === 'to') {
              rosArr.push(ros)
            }
          }
          commit('receive_roster', rosArr)
          // roster = roster.filters(ros => {ros.subscription === 'both' || ros.subscription === 'to'})
          // commit('receive_roster',roster)
          // console.log(state.roster);
        },
      })
    },

    //发送文本信息
    sendTextMeg({
      state,
      commit
    }, {
      to,
      msg
    }) {
      // console.log('send message');
      var id = state.WebIM.conn.getUniqueId(); // 生成本地消息id
      var message = new state.WebIM.message('txt', id); // 创建文本消息
      message.set({
        msg, // 消息内容
        to, // 接收消息对象（用户id）
        roomType: false,
        ext: {}, //扩展消息
        success: function (id) {
          console.log('send private text Success');
          commit('addNewMsg', {
            id,
            data: msg,
            from: to,
            self: true
          })
        }, // 对成功的相关定义，sdk会将消息id登记到日志进行备份处理

        fail: function (e) {
          console.log("Send private text error", e);
        } // 对失败的相关定义，sdk会将消息id登记到日志进行备份处理
      });
      state.WebIM.conn.send(message.body);
    },

    //获取群组列表
    getGroup({
      state,
      commit
    }) {
      var options = {
        success: function (resp) {
          // console.log("Response: ", resp.data)
          commit('receive_group', resp.data)
        },
        error: function (e) {
          console.log(e);
        }
      }
      state.WebIM.conn.getGroup(options);
    },

    //创建群组
    createGroupNew({
      state
    }, groupName) {
      var options = {
        data: {
          groupname: groupName, // 群组名
          desc: 'info', // 群组描述
          members: [state.user.username], // 用户名组成的数组
          public: true, // pub等于true时，创建为公开群
          approval: false, // approval为true，加群需审批，为false时加群无需审批
        },
        success: function (respData) {
          console.log(respData)
        },
      };
      state.WebIM.conn.createGroupNew(options);
    },

    //获取群组信息
    getGroupInfo({
      state
    }, gid) {
      return new Promise((resolve,reject) => {
        var options = {
          groupId: gid, //群组id
          success: function (resp) {
            // console.log("groupInfo: ", resp.data[0].name);
            // state.title = resp.data[0].name
            resolve(resp)
          },
          error: function () {
            // console.log('获取群组信息出错！')
            reject()
          }
        };
        state.WebIM.conn.getGroupInfo(options);
      })
    },

    //退出群组
    quitGroup({
      state
    }, gid) {
      var leaveGroup = function () {
        var option = {
          // to: 'asdfghj',
          groupId: gid,
          success: function () {
            console.log('You leave room succeed!');
          },
          error: function () {
            console.log('Leave room faild');
          }
        };
        state.WebIM.conn.quitGroup(option);
      };
    },

    //获取群组成员
    listGroupMember({
      state
    }, gid) {
      var pageNum = 1,
        pageSize = 1000;
      var options = {
        pageNum: pageNum, // 页码
        pageSize: pageSize, // 预期每页获取的记录数
        groupId: gid,
        success: function (resp) {
          console.log("群组成员: ", resp.data)
          state.group = resp.data
        },
        error: function (e) {
          console.log(e)
        }
      };
      state.WebIM.conn.listGroupMember(options);
    },

    //将好友设为管理员
    setAdmin({
      state
    }, {
      gid,
      user
    }) {
      var options = {
        groupId: gid, // 群组id
        username: user, // 用户名
        success: function (resp) {
          console.log('成功设为管理员 =>', resp)
        },
        error: function (e) {
          console.log(e)
        }
      };
      state.WebIM.conn.setAdmin(options);
    },

    //将好友加入群组
    inviteToGroup({
      state,
      dispatch
    }, {
      user,
      gid
    }) {
      var option = {
        users: [user],
        groupId: gid,
        success: function () {
          let obj = {
            gid: gid,
            user: user
          }
          dispatch('setAdmin', obj)
        },
        error: function (e) {
          console.log('invite error', e)
        }
      };
      state.WebIM.conn.inviteToGroup(option);
    },

    // 向群组发出入群申请
    joinGroup({
      state
    }, gid) {
      return new Promise((resolve, reject) => {
        var options = {
          groupId: gid, // 群组ID
          success: function (resp) {
            resolve(resp)
          },
          error: function (e) {
            if (e.type == 17) {
              reject()
            }
          }
        };
        state.WebIM.conn.joinGroup(options);
      })
    },

    //管理员同意用户加入群
    agreeJoinGroup({
      state
    }, gid) {
      var opts = {
        applicant: state.user.username, // 申请加群的用户名
        groupId: gid, // 群组ID
        success: function (resp) {
          console.log('申请成功！');
        },
        error: function (e) {
          console.log(e)
        }
      };
      state.WebIM.conn.agreeJoinGroup(opts);
    },

    // 群组发送文本消息
    sendGroupText({
      state,
      commit
    }, {
      gid,
      content
    }) {
      var id = state.WebIM.conn.getUniqueId(); // 生成本地消息id
      var msg = new state.WebIM.message('txt', id); // 创建文本消息
      var option = {
        msg: content, // 消息内容
        to: gid, // 接收消息对象(群组id)
        roomType: false, // 群聊类型，true时为聊天室，false时为群组
        ext: {}, // 扩展消息
        success: function () {
          console.log('send room text success');
          commit('addGRoupMsg', {
            id,
            data: content,
            from: state.user.username,
            to:gid
          })
        }, // 对成功的相关定义，sdk会将消息id登记到日志进行备份处理
        fail: function () {
          console.log('failed');
        } // 对失败的相关定义，sdk会将消息id登记到日志进行备份处理
      };
      msg.set(option);
      msg.setGroup('groupchat'); // 群聊类型
      state.WebIM.conn.send(msg.body);
    }

  }
})