<template>
  <div class="chatList">
    <Header></Header>
    <div class="scoll_wrapper" ref="wrapper">
      <ul>
        <li v-for="(value,key) in msgDataSource" :key="key" @click="toChatbox(key)">
          <img src="../assets/image/timg.jpg" alt />
          <div class="content">
            <h3>{{key}}</h3>
            <span>{{value[value.length - 1].data}}</span>
          </div>

          <div class="unread">
            <a-badge :count="getnum(value)" />
          </div>
          <!-- <p>{{value}}</p> -->
        </li>
        <li v-for="(value,key) in groupMsgData" :key="key" @click="toChatgroup(key)">
          <img src="../assets/image/qq-group.jpeg" alt />
          <div class="content">
            <h3>groupid:{{key}}</h3>
            <span>{{value[value.length - 1].data}}</span>
          </div>

          <div class="unread">
            <a-badge :count="getnum(value)" />
          </div>
        </li>
      </ul>
    </div>

    <Tabbar></Tabbar>
  </div>
</template>

<script>
import Header from "../components/Header";
import Tabbar from "../components/Tabbar";
import BScroll from "better-scroll";
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  name: "messageView",
  data() {
    return {};
  },
  computed: {
    ...mapState(["msgDataSource", "groupMsgData"]),
    // ...mapGetters(["getnum"])
    getnum() {
      return function(arr) {
        // console.log(arr);
        let newarr = [];
        arr.forEach(element => {
          if (element.badge && element.badge === 1) {
            newarr.push(element);
          }
        });
        // console.log(newarr);
        return newarr.length;
      };
    }

    //获取群组名
    // getGroupName() {
    //   return function(key) {
    //     let name = 'group'
    //     this.getGroupInfo(key).then((e) => {
    //       // console.log(e)
    //       // return e.data[0].name
    //       name = e.data[0].name
    //       return name
    //     })
    //   };
    // }
  },
  components: {
    Header,
    Tabbar
  },
  methods: {
    ...mapMutations(["updateUnread", "updateGroupUnread"]),
    ...mapActions(["getGroupInfo"]),
    toChatbox(name) {
      this.updateUnread(name);
      this.$store.commit("receive_activeUser", name);
      this.$router.push({ name: "chatbox", params: { username: name } });
    },
    toChatgroup(gid) {
      this.updateGroupUnread(gid)
      this.$store.commit("receive_activegroup", gid);
      this.$router.push({ name: "chatgroup", params: { groupid: gid } });
      this.getGroupInfo(gid).then(
        name => {
          this.$store.state.title = name.data[0].name;
        },
        () => {
          // console.log("获取群组信息失败！");
          return
        }
      ); //获取群组信息
    },
    initBetterScroll() {
      if (this.wrapperScroll) {
        // 如果存在代表menuScroll已经初始化过一次了
        this.wrapperScroll.refresh(); // 重新计算容器与内容的高度,无需重新实例化新对象
      } else {
        this.wrapperScroll = new BScroll(this.$refs.wrapper, {
          click: true
        }); // 该页面被注销好去清除他
      }
    }
  },
  beforeMount() {
    //读取本地存储
    // console.log(this.$store.state.user.username);
    let chatMsg = localStorage.getItem(this.$store.state.user.username);
    if (chatMsg) {
      this.$store.state.msgDataSource = JSON.parse(chatMsg);
    }

    let groupMsg = localStorage.getItem('groupMsg');
    if (groupMsg) {
      this.$store.state.groupMsgData = JSON.parse(groupMsg);
    }
  },
  mounted() {
    this.initBetterScroll();
  },
  beforeDestroy() {
    this.wrapperScroll.destroy();
    this.wrapperScroll = null;
  }
};
</script>

