.scoll_wrapper {
  width: 100vw;
  height: 75vh;
  ul {
    margin: 0;
    padding: 0;
    li {
      padding: 10px 20px 10px 20px;
      display: flex;
      img {
        width: 45px;
        height: 45px;
        border-radius: 50%;
        flex: 0 0 45px;
        margin-right: 15px;
      }
      .content {
        flex: 1;
        text-align: left;
        overflow: hidden;
        h3 {
          margin: 0;
        }
        span {
          font-size: 8px;
          color: $q-lightgray;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
      .unread {
        margin-left: 10px;
        flex: 0 0 32px;
        line-height: 45px;
      }
    }
  }
}
</style>