<template>
  <div class="navbar">
    <div :class="['tab', {active:activeTab === 'messageTital'}]" @click="switchRouterA">
      <a-icon type="message" class="icon" />
      <div class="tabText">
        <span>消息</span>
      </div>
    </div>

    <div :class="['tab', {active:activeTab === 'contacts'}]" @click="switchRouterB">
      <a-badge :count="messages.length" dot>
        <a-icon type="contacts" class="icon" />
      </a-badge>
      <div class="tabText">
        <span>通讯录</span>
      </div>
    </div>

    <div :class="['tab', {active:activeTab === 'user'}]" @click="switchRouterC">
      <a-icon type="user" class="icon" />
      <div class="tabText">
        <span>我</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState,mapMutations } from "vuex";
export default {
  name: "Tabbar",
  data() {
    return {
      // count:1
    };
  },
  computed: {
    ...mapState(["messages","activeTab"])
  },
  methods: {
    ...mapMutations(['change_tab']),
    switchRouterA() {
      let str = 'messageTital'
      this.change_tab(str)
      this.$router.push({
        path: "/home/message"
      });
    },
    switchRouterB() {
      let str = 'contacts'
      this.change_tab(str)
      this.$router.push({
        path: "/home/contacts"
      });
    },
    switchRouterC() {
      let str = 'user'
      this.change_tab(str)
      this.$router.push({
        path: "/home/user"
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.navbar {
  height: 10vh;
  width: 100%;
  max-width: 380px;
  position: fixed;
  bottom: 0;
  display: flex;
  text-align: center;
  background-color: $q-white;
  z-index: 999;
  .tab {
    padding: 10px 0;
    display: inline-block;
    flex: 1;
    text-decoration: none;
    font-size: 10px;
    .icon {
      font-size: 25px;
      font-weight: bold;
    }
    .tabText {
      span {
        font-size: 5px;
        color: rgba(0, 0, 0, 0.65);
      }
    }
    &.active {
      .icon {
        color: $q-blue;
      }
      .tabText {
        span {
          color: $q-blue;
        }
      }
    }
  }
}
</style>
